import { useState } from "react";
import { useHistory } from "react-router-dom";

export default function CreateValue() {
  // skapar ngot att lagra inputen i
  const [valueTitle, setValueTitle] = useState(" ");
  const [valueDesc, setValueDesc] = useState(" ");
  const [valueCategori, setvalueCategori] = useState("Fritid");
  //fr att kunna visa loading msg
  const [isPending, setIsPending] = useState(false);
  // obj som representerar history
  const history = useHistory();

  const handleSubmit = (e) => {
    e.preventDefault();
    // tar vrderna frn STATE och gr ett objekt av dom
    const value = {
      title: valueTitle,
      desc: valueDesc,
      categori: valueCategori,
    };
    setIsPending(true);

    fetch("http://localhost:3000/valueList", {
      // berttar fr servern att det r en post med JSON data
      method: "POST",
      headers: { "Content-Type": "application/json" },
      //datan som ska skickas
      body: JSON.stringify(value),
    }).then(() => {
      console.log(value);
      console.log("nytt vrde tillagt");
      setIsPending(false);
      history.push("/");
    });
    // gr tillbaka ett steg
    // history.go(-1);
  };
  return (
    <div className="create">
      <h2> Du r p create!</h2>
      <form onSubmit={handleSubmit}>
        <lable>Vrdering:</lable>
        <input
          type="text"
          required
          value={valueTitle}
          onChange={(e) => setValueTitle(e.target.value)}
        />

        <lable>Beskrivning:</lable>
        <input
          type="text"
          required
          value={valueDesc}
          onChange={(e) => setValueDesc(e.target.value)}
        />
        <select
          value={valueCategori}
          onChange={(e) => setvalueCategori(e.target.value)}
        >
          <option value="Arbete"> Arbete </option>
          <option value="Fritid"> Fritid </option>
        </select>
        {/* frsta knappen visas om isPending inte sant   */}
        {!isPending && (
          <button onChange={(e) => handleSubmit(e)}>Lgg till vrdering</button>
        )}
        {isPending && (
          <button disable onChange={(e) => handleSubmit(e)}>
            Laddar upp vrdering
          </button>
        )}
        <p> {valueTitle}</p>
        <p> {valueDesc}</p>
        <p> {valueCategori}</p>
      </form>
    </div>
  );
}
