import { useState, useEffect } from "react";

// custom hooks mste brja med use
const useFetch = (url) => {
  const [data, setData] = useState(null);
  const [isPending, setIsPending] = useState(true);
  const [error, setError] = useState(null);

  useEffect(() => {
    const abortCont = new AbortController();
    fetch(url, { signal: abortCont.signal })
      .then((res) => {
        // finns det en error?
        if (!res.ok) {
          throw Error("Kunde inte hmta datan");
        }
        return res.json();
      })
      .then((data) => {
        // nr values r hmtade s ndras isPending frn sant till falsk
        setIsPending(false);
        setData(data);
        // s err frsvinner om man vid senare tillflle lyckas hmta
        setError(null);
      })
      .catch((err) => {
        // fr att se till att err msg gr fram slnge det inte r pga abortcontroller
        if (err.name === "AbortError") {
          console.log("fetch aborted");
        } else {
          // s den inte visar Laddar om error
          setIsPending(false);
          // err state updateras s den syns med error message
          setError(err.message);
        }
      });
    // s att man inte fr error fr att man frsker ndra state p en unmountad component
    return () => abortCont.abort();
  }, [url]);

  return { data, isPending, error };
};
export default useFetch;
