import { Typography } from "@mui/material";
import React, { useState, useEffect } from "react";
import ExerciseAppbar from "./ExerciseAppbar";
import ValuePicker from "./ValuePicker";
import Slide from "@mui/material/Slide";
import Container from "@mui/material/Container";
import Headline from "./Headline";
import values from "./values";
import { useLocation } from "react-router-dom";
import StepperExercise from "./StepperExcercise";
import Fade from "@mui/material/Fade";

export default function ValueList() {
  // deconstruct return obj {data:values} dper om data till values

  const location = useLocation();
  const localValues = localStorage.getItem("userValues10");
  const valueArray = location.state ? location.state : JSON.parse(localValues);

  const [slide, setSlide] = useState(false);

  useEffect(() => {
    setSlide(true);
  }, []); // Only re-run the effect if count changes
  // useEffect krs p varje render, hr hmtas data frn JSON server

  return (
    <div>
      <ExerciseAppbar header={"Vrdekompassen"} step={"Steg 2 av 3"} />
      <StepperExercise
        activeStep={1}
        steps={["Vlj tio", "Vlj fem", "Prioritera"]}
      />
      <Fade in={slide} mountOnEnter unmountOnExit>
        <Container sx={{ backgroundColor: "white" }}>
          <div className="textWrapper">
            <Headline text="Vlj dina vrderingsord" />{" "}
            <Typography variant="body1">
              {/* finns x antal ord ls igenom alla innan du gr vidare 
                nr du r klar: sker att du vill g vidare (har du lst alla?) */}
              Vlj nu ut de fem absolut viktigaste vrderingarna av de tio du
              valde tidigare. Har du svrt att vlja testa terigen att stlla
              dig frgan: hur hade jag velat bli ihgkommen?
            </Typography>
            <br></br>
            {/* om values finns s krs ValuesComponet  */}
            {values && (
              <ValuePicker
                values={valueArray}
                nrsToPick={5}
                next="/varderingsPrioritering"
                back="/valj10varder"
                saveAs=""
              />
            )}
          </div>
        </Container>
      </Fade>
    </div>
  );
}
