import { useLocation } from "react-router-dom";
import React, { useState, useEffect } from "react";
import Slide from "@mui/material/Slide";
import ExerciseAppbar from "./ExerciseAppbar";
import { Divider } from "@mui/material";
import Rating from "@mui/material/Rating";
import Box from "@mui/material/Box";
import StarIcon from "@mui/icons-material/Star";
import BackToHomeButton from "./BackToHomeButton";
import Typography from "@mui/material/Typography";
import Avatar from "@mui/material/Avatar";
import StepperExercise from "./StepperExcercise";

const labels = {
  0: "Vldigt Dligt",

  1: "Ganska Dligt",

  2: "Rtt bra",

  3: "Bra",

  4: "Mycket Bra",

  5: "Vldigt Bra",
};

function ValueResults(obj) {
  const [slide, setSlide] = useState(false);
  const [value, setValue] = React.useState(3);
  const [hover, setHover] = React.useState(-1);
  const [ShowValueInfo, setShowValueInfo] = React.useState(0);

  useEffect(() => {
    postResult();
    setSlide(true);
  }, []);

  const location = useLocation();
  const valueArray = location.state;

  const results = valueArray.sort((a, b) => {
    return b.pts - a.pts;
  });

  const user = {
    values: results,
    state: 1.1,
  };

  const handleShowValueInfo = (index) => {
    console.log(index);
    setShowValueInfo(index);
  };

  const postResult = () => {
    fetch("http://localhost:3000/user", {
      // berttar fr servern att det r en post med JSON data
      method: "POST",
      headers: { "Content-Type": "application/json" },
      //datan som ska skickas
      body: JSON.stringify(user),
    }).then(() => {
      // visa confomration att det sparats hr?
    });
  };

  return (
    <Slide direction="left" in={slide} mountOnEnter unmountOnExit>
      <div>
        <ExerciseAppbar header={"Vrderinskompassen"} step={"3 av 3"} />
        <StepperExercise
          activeStep={3}
          steps={["Vlj tio", "Vlj fem", "Prioritera"]}
        />
        <div className="textWrapper">
          <div>
            <Typography
              variant="h2"
              gutterBottom
              align="center"
              sx={{ mt: "30px" }}
            >
              Dina fem viktigaste vrderingar
            </Typography>
            <Divider></Divider>
            <Typography
              variant="body1"
              gutterBottom
              sx={{ mb: "25px", mt: "25px", maxWidth: "725px", mx: "auto" }}
            >
              Bra jobbat, ta ett djupt andetag och tacka dig sjlv, du har
              precis gjort ngot viktigt och meningsfullt fr din egna skull. Ta
              en noggrann titt p dina vrderingar. Vi kommer utforska vad dom
              betyder fr dig och hur du kan bli bttre p att lta dom vgleda
              dig genom livet i vning 3.
            </Typography>
            <div className="noStyleList">
              {results.map(({ title, desc, pts }, index) => {
                return (
                  <Slide
                    direction="up"
                    in={true}
                    timeout={index}
                    mountOnEnter
                    unmountOnExit
                  >
                    <Box
                      onClick={() => handleShowValueInfo(index)}
                      key={index}
                      className={
                        ShowValueInfo === index ? "transform90" : "not active"
                      }
                      sx={{
                        display: "table",
                        mx: "auto",
                        transform: "scale(1)",
                        margin: "auto",
                        mt: "15px",
                        boxShadow: 7,
                        borderColor: "grey.500",
                        width: "95%",
                        height: "200px",
                        maxWidth: "450px",
                        bgColor: "primary.main",
                        color: "primary.main",
                        borderRadius: "6px",
                        mb: "25px",
                        "&:hover": {
                          bgColor: "scale(1.1)",
                        },
                      }}
                    >
                      <Avatar
                        sx={{
                          position: "absolute",
                          bgcolor: "primary.extraLight",
                          color: "primary.main",
                          float: "left",
                          width: 30,
                          height: 30,
                          mt: "15px",
                          ml: "15px",
                          fontWeight: "bold",
                        }}
                      >
                        <Typography
                          variant="body1"
                          sx={{ fontWeight: "bold", userSelect: "none" }}
                        >
                          {" "}
                          {index + 1}
                        </Typography>{" "}
                      </Avatar>

                      <li key={index}>
                        <Typography
                          variant="h2"
                          sx={{
                            textAlign: "center",
                            paddingBottom: "5px",
                            pt: "25px",
                            ml: "30px",
                            mr: "30px",
                            pb: "5px",
                            mt: "5px",

                            userSelect: "none",
                          }}
                        >
                          {title}
                        </Typography>
                        <Divider
                          className="white"
                          sx={{ width: "65%", mx: "auto" }}
                        ></Divider>
                        <Typography
                          variant="body1"
                          sx={{
                            p: "15px",
                            fontSize: "1.1rem",
                            userSelect: "none",
                            ml: "25px",
                            mr: "25px",
                          }}
                        >
                          {" Jag vill " + desc.toLocaleLowerCase()}
                        </Typography>
                      </li>
                    </Box>
                  </Slide>
                );
              })}
            </div>
            <br></br>
            <Typography
              variant="h3"
              gutterBottom
              sx={{
                mb: "25px",
                mt: "25px",
                maxWidth: "725px",
                textAlign: "center",
              }}
            >
              Hur bra tycker du att du lever efter dessa fem vrderingar idag?
            </Typography>
            <Divider />
            <Box
              sx={{
                mx: "auto",
                width: 200,
                display: "flex",
                alignItems: "center",
                height: "40px",
                mt: "25px",
              }}
            >
              <Rating
                name="hover-feedback"
                value={value}
                precision={1}
                onChange={(event, newValue) => {
                  setValue(newValue);
                }}
                onChangeActive={(event, newHover) => {
                  setHover(newHover);
                }}
                emptyIcon={
                  <StarIcon style={{ opacity: 0.55 }} fontSize="inherit" />
                }
              />
              {value !== null && (
                <Box sx={{ ml: 2 }}>{labels[hover !== -1 ? hover : value]}</Box>
              )}
            </Box>
            <Typography
              variant="h3"
              sx={{
                textAlign: "center",
                paddingBottom: "5px",
                pt: "25px",
                pb: "10px",
                mt: "5px",
                mr: "45px",
                userSelect: "none",
              }}
            >
              Frsta vningen klar!
            </Typography>
            <Divider></Divider>
            <Typography variant="body1" gutterBottom sx={{ pt: "10px" }}>
              I nsta vning, "Dina livsomrden" kommer vi fortstta frdjupa
              oss i vrderingar. Dr kommer vi utforska hur dina vrderingar ser
              ut i olika livsomrden och hur du vrderar olika delarna av livet.
            </Typography>
          </div>
          <Box textAlign="center">
            {" "}
            <BackToHomeButton />{" "}
          </Box>
        </div>
      </div>
    </Slide>
  );
}

export default ValueResults;
