import React, { useState, useEffect } from "react";
import { useHistory, createBrowserHistory } from "react-router-dom";
import { Button, Typography } from "@mui/material";
import Box from "@mui/material/Box";
import { Divider } from "@mui/material";
import Slide from "@mui/material/Slide";
import Container from "@mui/material/Container";
import UndoRoundedIcon from "@mui/icons-material/UndoRounded";
import LinearProgress from "@mui/material/LinearProgress";
import ItsDoneComponent from "./ItsDoneComponent";
import BackButton from "./BackButton";
import ArrowForwardIosIcon from "@mui/icons-material/ArrowForwardIos";
import useMediaQuery from "@mui/material/useMediaQuery";
import Fade from "@mui/material/Fade";
import Headline from "./Headline";
import ProgressTracker from "./utility/ProgressTracker";

function CardSorter({ valueArray: arryOfValues, next, back, saveAs }) {
  const history = useHistory();

  const [valueArray] = useState(arryOfValues);
  const [showTop, setShowTop] = useState(0);
  const [showBottom, setShowBottom] = useState(1);
  const [startValue, setstartValue] = useState(2);
  const [slide, setSlide] = useState(false);
  const [cardsSorted, setcardsSorted] = useState(0);
  const [totalClicks, settotalClicks] = useState(0);
  const [ShowItsDone, setShowItsDone] = useState(false);
  const [pickedCards, setPickedCards] = useState([]);
  const [isItDone, setisItDone] = useState(false);
  const smallScreen = useMediaQuery("(max-width:322px)");

  const goToResult = () => {
    localStorage.setItem(saveAs, JSON.stringify(valueArray));
    history.push({
      pathname: next,
      state: valueArray,
    });
  };

  useEffect(() => {
    setSlide(true);

    countTotalClicks();
  }, []);

  // Anvnds fr att updater totalClicks
  const countTotalClicks = () => {
    let clicks = 0;
    arryOfValues.forEach((item, index) => {
      clicks = clicks + index;
    });
    console.log("clicks :" + clicks);
    settotalClicks(clicks);
  };

  const addPts = (id) => {
    valueArray.forEach((item, index) => {
      if (id === item.id) {
        item.pts = item.pts + 1;
      }
    });
  };

  // krs vid sista kortet fr att ta bort frsta kortet ur displayn
  const restartArr = () => {
    console.log("arr slut");
    // en counter som kar med 1 fr varje iteration genom kortleken
    setstartValue(startValue + 1);
    // updaterar s att nsta item visas
    setShowBottom(startValue);

    setShowTop(showTop + 1);
    console.log("showtop / startvalue" + startValue);
  };

  // id kommer vara samma tills den startar om
  const clickTop = ({ index, id }) => {
    console.log("cardsSorted");
    console.log(cardsSorted);

    setPickedCards((prevArray) => [...prevArray, id]);

    if (cardsSorted !== totalClicks) {
      console.log("setcardsSorted(cardsSorted + 1);");
      setcardsSorted(cardsSorted + 1);
    }

    if (isItDone) {
      setShowItsDone(true);
      return;
    }
    //lgger till pts
    addPts(id);
    // !isItDone fr att den inte ska ndra sig p click vid frdigt

    if (startValue === valueArray.length) {
      // om vningen r klar
      console.log("done");
      console.log(valueArray);
      setisItDone(true);
      /*  history.push({
        pathname: "/valueResults",
        state: valueArray,
      }); */

      return;
    }

    // om vi r p sista kortet
    if (showBottom + 1 === valueArray.length) {
      setstartValue(startValue + 1);
      setShowTop(showTop + 1);
      setShowBottom(startValue);
    } else {
      setShowBottom(showBottom + 1);
    }
  };

  const clickBottom = ({ index, id }) => {
    setPickedCards((prevArray) => [...prevArray, id]);

    console.log("cardsSorted");
    console.log(cardsSorted);
    console.log("totalClicks");
    console.log(totalClicks);
    console.log(cardsSorted);
    if (cardsSorted !== totalClicks) {
      setcardsSorted(cardsSorted + 1);
    }

    //om vningen r klart
    if (isItDone) {
      setShowItsDone(true);
      return;
    }

    //lgger till pts
    addPts(id);

    if (startValue === valueArray.length) {
      setisItDone(true);
      return;
    }
    index + 1 === valueArray.length ? restartArr() : setShowBottom(index + 1);
  };

  const goBack = () => {
    console.log("cardsSorted");
    console.log(cardsSorted);
    // fr att rtt antal klick ska vara kvar
    setcardsSorted(cardsSorted - 1);

    // tar bort pts frn kort nr man backar
    const id = pickedCards[pickedCards.length - 1];
    pickedCards.pop();

    if (isItDone) {
      addPts(id);
      setisItDone(false);
      return;
    }

    valueArray.forEach((item, index) => {
      if (id === item.id) {
        item.pts = item.pts - 1;
      }
    });

    // om vi r p frsta kortet i bottenhgen, dvs om vi mste ndra showTop
    if (startValue === showBottom + 1) {
      console.log("r p frsta kortet i top, dvs om vi mste ndra showTop");
      setstartValue(startValue - 1);
      setShowTop(showTop - 1);

      setShowBottom(valueArray.length - 1);

      console.log("-------------");
      console.log(startValue);
      console.log(valueArray.length);
    } else if (showBottom >= 0) {
      setShowBottom(showBottom - 1);
    }

    //ta bort points
  };

  return (
    <div>
      <ProgressTracker
        nrsToPick={totalClicks}
        nrsPicked={cardsSorted}
        isItDone={isItDone}
      />

      <Slide direction="left" in={slide}>
        <Container>
          <Box textAlign="center" sx={{ mt: "15px", mb: "15px" }}>
            <Button
              disabled={cardsSorted === 0 ? true : false}
              variant="outlined"
              startIcon={<UndoRoundedIcon />}
              sx={{
                justifyContent: "center",
                margin: "auto",
              }}
              onClick={() => goBack()}
            >
              ngra val
            </Button>
          </Box>

          <div className="relative">
            <Box>
              {valueArray.map(({ title, desc, id }, index) => {
                return (
                  <Fade
                    timeout={{ enter: 1200, exit: 1200 }}
                    in={showTop === index}
                  >
                    <Box
                      key={"topCard" + id.toString()}
                      className={index === showTop ? "show" : "hidden"}
                      onClick={() => clickTop({ index: index, id: id })}
                      sx={{
                        display: "table",
                        mx: "auto",
                        margin: "auto",
                        boxShadow: 6,
                        width: "100%",
                        height: "200px",
                        maxWidth: "450px",
                        backgroundColor: "white",
                        borderRadius: "6px",
                        userSelect: "none",

                        color: "primary",
                        "&:hover": {
                          transform: "scale(1.02)",
                        },
                      }}
                    >
                      {isItDone && (
                        <Typography
                          variant="h3"
                          sx={{
                            textAlign: "center",
                            paddingBottom: "5px",
                            p: "25px",
                            pb: "10px",

                            padding: "10px",
                            mt: "45px",
                          }}
                        >
                          {" "}
                          Du r nu frdig med vningen! Tryck p "Se resultat"
                          knappen fr att se ditt resultat.
                        </Typography>
                      )}

                      <Box className={isItDone && "hiddenCard"}>
                        <Typography
                          variant="h2"
                          sx={{
                            textAlign: "center",
                            paddingBottom: "5px",
                            pt: "25px",
                            pb: "10px",
                            mt: "5px",
                          }}
                        >
                          {title}
                        </Typography>

                        <>
                          <Divider
                            className="white"
                            sx={{ width: "85%", mx: "auto" }}
                          ></Divider>
                        </>

                        <Typography
                          variant="body1"
                          align="center"
                          sx={{
                            p: "30px",
                            pt: "15px",
                            fontSize: "1.1rem",
                          }}
                        >
                          {desc}
                        </Typography>
                      </Box>
                    </Box>
                  </Fade>
                );
              })}
            </Box>
          </div>
          <Box className={isItDone && "hiddenCard"}>
            <Divider sx={{ width: "100%", mt: "30px", mb: "30px", mx: "auto" }}>
              {" "}
              <Typography variant="h3" margin={"auto"} gutterBottom>
                Eller..
              </Typography>
            </Divider>

            <div className="relative">
              {" "}
              {valueArray.map(({ title, desc, id }, index) => {
                return (
                  <Fade
                    timeout={{ enter: 1200, exit: 1200 }}
                    in={showBottom === index}
                  >
                    <Box
                      key={"bottomCard" + id.toString()}
                      className={showBottom === index ? "show" : "hidden"}
                      onClick={() => clickBottom({ index: index, id: id })}
                      sx={{
                        display: "table",
                        mx: "auto",
                        transform: "scale(1)",
                        margin: "auto",
                        boxShadow: 10,
                        borderColor: "grey.500",
                        width: "95%",
                        height: "200px",
                        maxWidth: "450px",
                        backgroundColor: "white",
                        borderRadius: "6px",
                        userSelect: "none",

                        color: "primary",
                        "&:hover": {
                          transform: "scale(1.02)",
                        },
                      }}
                    >
                      <Typography
                        variant="h2"
                        sx={{
                          textAlign: "center",
                          paddingBottom: "5px",
                          pt: "25px",
                          pb: "10px",
                          mt: "5px",
                        }}
                      >
                        {title}
                      </Typography>

                      <>
                        <Divider
                          className="white"
                          sx={{ width: "85%", mx: "auto" }}
                        ></Divider>
                      </>

                      <Typography
                        variant="body1"
                        align="center"
                        sx={{
                          p: "30px",
                          pt: "15px",
                          fontSize: "1.1rem",
                        }}
                      >
                        {desc}
                      </Typography>
                    </Box>
                  </Fade>
                );
              })}
            </div>
          </Box>
          <Box
            sx={{
              transform: smallScreen ? "scale(0.9)" : null,
              width: "100%",
            }}
          >
            <BackButton data={valueArray} goTo={back} />

            {isItDone && (
              <Button
                variant="contained"
                color="primary"
                aria-label="G till resultat"
                endIcon={<ArrowForwardIosIcon />}
                onClick={goToResult}
                sx={{ float: "right", mb: "15px", mt: "45px", mr: "15px" }}
              >
                Se Resultat
              </Button>
            )}

            {!isItDone && (
              <Button
                variant="contained"
                disabled="true"
                color="success"
                aria-label="Gr frdigt vningen fr att kunna g vidare till resultat"
                endIcon={<ArrowForwardIosIcon />}
                sx={{ float: "right", mb: "15px", mt: "45px", mr: "15px" }}
              >
                {totalClicks - cardsSorted} kort kvar
              </Button>
            )}
          </Box>
        </Container>
      </Slide>
      {ShowItsDone && (
        <ItsDoneComponent
          title="Bra jobbat!"
          text="Du r nu klar med vrderingskompassen, tryck p knappen SE RESULTAT fr att se ditt resultat"
          option2="Okej!"
        />
      )}
    </div>
  );
}

export default CardSorter;
