import ExerciseAppbar from "../ExerciseAppbar.js";
import Headline from "../Headline";
import BackButton from "../BackButton";
import Typography from "@mui/material/Typography";
import Button from "@mui/material/Button";
import React, { useState, useEffect } from "react";
import { useHistory, useLocation } from "react-router-dom";
import Container from "@mui/material/Container";
import ArrowBackIosIcon from "@mui/icons-material/ArrowBackIos";
import ArrowForwardIosIcon from "@mui/icons-material/ArrowForwardIos";
import Box from "@mui/material/Box";
import Slider from "@mui/material/Slider";
import Stack from "@mui/material/Stack";
import TextField from "@mui/material/TextField";
import StepperExercise from "../StepperExcercise";
import ProgressTracker from "../utility/ProgressTracker";

function LifeAreasPriority() {
  const [lifeAreasState, setLifeAreas] = React.useState([]);
  const [today, setToday] = React.useState();
  const [todayError, setTodayError] = React.useState(false);
  const [howImportent, setHowImportent] = React.useState();
  const [howImportentError, setHowImportentError] = React.useState(false);
  const [obstacle, setObstacle] = React.useState("");
  const [lifeAreasDone, setLifeAreasDone] = React.useState(0);
  const [isItDone, setIsItDone] = React.useState(false);
  const [showLifeArea, setShowLifeArea] = React.useState(0);

  const howHappyLable = [
    "Helt och hllet missnjd",
    "Mycket missnjd",
    "Missnjd",
    "Ganska missnjd",
    "Lite missnjd",
    "Varken njd eller missnjd",
    "Lite njd",
    "Ganska njd",
    "Njd",
    "Mycket njd",
    "Helt och hllet njd",
  ];

  const howImportentLable = [
    "Helt och hllet oviktigt",
    "Mycket oviktigt",
    "Oviktigt",
    "Ganska oviktigt",
    "Lite oviktigt",
    "Varken viktigt eller oviktigt",
    "Lite viktigt",
    "Ganska viktigt",
    "Viktigt",
    "Mycket viktigt",
    "S viktigt det kan bli",
  ];

  let saveAs = "prioLifeAreas";

  useEffect(() => {
    if (showLifeArea === lifeAreas.length) {
      setIsItDone(true);
    }
  }, [showLifeArea]);

  function valuetext(value) {
    return "${value}";
  }
  const history = useHistory();

  const nextPage = () => {
    localStorage.setItem(saveAs, JSON.stringify(lifeAreasState));

    history.push({
      pathname: "/livsomraden-varderingar",
      state: lifeAreasState,
    });
  };

  const location = useLocation();
  const localLifeAreas = localStorage.getItem("lifeAreas");
  const lifeAreas = location.state
    ? location.state
    : JSON.parse(localLifeAreas);
  // location.state;
  console.log(lifeAreas);

  const nextLifeArea = () => {
    console.log("hehe");
  };
  const resetVaules = () => {
    setToday();

    setHowImportent();
  };

  const handleSubmit = ({ title }) => {
    if (today === undefined && howImportent === undefined) {
      console.log("noToday");
      console.log(today);
      console.log("howImportent");
      console.log(howImportent);
      setTodayError(true);
      setHowImportentError(true);
      return;
    }
    if (today === undefined) {
      console.log("noToday");
      setTodayError(true);
      return;
    }
    if (howImportent === undefined) {
      console.log("noImportente");
      setHowImportentError(true);
      return;
    }
    // det okjekt som ska sparas
    let lifeArea = {
      title: title,
      today: today,
      howImportent: howImportent,
      obstacle: obstacle,
      diff: howImportent - today,
    };
    console.log("---------------------");
    console.log(lifeAreasState);
    // ((prevValues) => [...prevValues, lifeArea]) stter in den nya "lifeArea" sist i lifeAres arrayn
    setLifeAreas((prevValues) => [...prevValues, lifeArea]);
    setShowLifeArea(showLifeArea + 1);
    resetVaules();
  };

  useEffect(() => {
    if (showLifeArea === lifeAreas.length) {
      setIsItDone(true);
    }
  }, [showLifeArea]);

  useEffect(() => {
    setHowImportentError(false);
  }, [howImportent]);

  useEffect(() => {
    setTodayError(false);
  }, [today]);

  const goBackOneCard = () => {
    console.log("backar");
  };

  return (
    <div>
      <ExerciseAppbar header={"Dina Livsomrden"} step={" "} />
      <ProgressTracker
        nrsToPick={lifeAreas.length}
        nrsPicked={showLifeArea}
        isItDone={isItDone}
      />
      <Container>
        <StepperExercise
          activeStep={1}
          steps={["Vlj omrden", "Prioritera", "Placeholder"]}
        />

        {isItDone && <Headline text="Bra jobbat" />}
        {!isItDone && <Headline text="Prioritera dina livsomrden" />}

        <Box sx={{ maxWidth: "725px", mx: "auto" }}>
          {isItDone && <Typography>Du r frdig! Duktig du r!</Typography>}
          {!isItDone && (
            <Typography>
              Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do
              eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut
              enim ad minim veniam, quis nostrud exercitation ullamco laboris
              nisi ut aliquip ex ea commodo consequat. Duis aute irure dolor in
              reprehenderit in voluptate velit esse cillum dolore eu fugiat
              nulla pariatur. Excepteur sint occaecat cupidatat. (0 = helt
              onjd; 10 = helt och hllet njd). 0 = helt oviktigt ; 10 =
              vldigt viktigt)
            </Typography>
          )}
        </Box>
        <Box>
          {lifeAreas.map(({ title }, index) => {
            // console.log(title);
            return (
              <Box
                className={index === showLifeArea ? "show" : "hidden"}
                boxShadow={10}
                key={index}
                sx={{
                  display: "table",
                  border: "1px solid lightgrey",
                  margin: "15px",
                  borderRadius: "6px",
                  mx: "auto",
                  mb: "50px",
                  maxWidth: "100%",
                  mt: "50px",
                  "&:hover": {
                    transform: "scale(1.02)",
                  },
                }}
              >
                <Box
                  boxShadow={0}
                  sx={{
                    color: "white",
                    padding: "15px",
                    pt: "20px",
                    pb: "20px",
                    borderRadius: "6px 6px 0px 0px",
                    position: "relative",
                  }}
                >
                  <Box className="inverted-border-radius-left"> </Box>
                  <Box className="inverted-border-radius-right"> </Box>

                  <Typography variant="h2" textAlign={"center"} sx={{}}>
                    {" "}
                    {title}
                  </Typography>
                </Box>
                <form onSubmit={handleSubmit}>
                  <Box
                    className={todayError && "errorBorder"}
                    sx={{
                      bgcolor: "primary.extraLight",
                      padding: "15px",
                      pt: "30px",
                      pb: "30px",
                      borderRadius: "0px 0px 0px 0px",
                    }}
                  >
                    <Typography
                      variant="h4"
                      textAlign={"center"}
                      sx={{ mb: "10px" }}
                    >
                      Hur njd r du med livsomrdet "{title}" idag?
                    </Typography>
                    {!todayError ? (
                      <Typography
                        variant="body1"
                        textAlign={"center"}
                        sx={{ mb: "10px" }}
                      >
                        - {howHappyLable[today]}{" "}
                      </Typography>
                    ) : (
                      <Typography
                        variant="body1"
                        textAlign={"center"}
                        sx={{ mb: "10px", color: "error.main" }}
                      >
                        Du mste svara p hur njd du r med detta omrde idag
                        fr att g vidare
                      </Typography>
                    )}

                    <Stack
                      spacing={2}
                      direction="row"
                      sx={{}}
                      justifyContent="center"
                    >
                      <Typography sx={{ fontWeight: "bold", pt: "3px" }}>
                        0
                      </Typography>
                      <Slider
                        aria-label={title}
                        defaultValue={5}
                        getAriaValueText={valuetext}
                        valueLabelDisplay="auto"
                        step={1}
                        min={0}
                        max={10}
                        sx={{ width: "75%" }}
                        onChange={(e) => setToday(e.target.value)}
                      />
                      <Typography sx={{ fontWeight: "bold", pt: "3px" }}>
                        10
                      </Typography>
                    </Stack>
                  </Box>

                  <Box
                    className={howImportentError && "errorBorder"}
                    sx={{ padding: "15px", pt: "30px", pb: "30px" }}
                  >
                    <Typography
                      variant="h4"
                      textAlign={"center"}
                      sx={{ mb: "10px" }}
                    >
                      Hur viktigt r livsomrdet "{title}" fr dig?
                    </Typography>

                    {!howImportentError ? (
                      <Typography
                        variant="body1"
                        textAlign={"center"}
                        sx={{ mb: "10px" }}
                      >
                        - {howImportentLable[howImportent]}
                      </Typography>
                    ) : (
                      <Typography
                        variant="body1"
                        textAlign={"center"}
                        sx={{ mb: "10px", color: "error.main" }}
                      >
                        Du mste vlja hur viktigt detta omrde r fr att g
                        vidare
                      </Typography>
                    )}

                    <Stack
                      spacing={2}
                      direction="row"
                      sx={{ mb: 1 }}
                      justifyContent="center"
                    >
                      <Typography sx={{ fontWeight: "bold", pt: "3px" }}>
                        {" "}
                        0{" "}
                      </Typography>{" "}
                      <Slider
                        aria-label={title}
                        defaultValue={5}
                        getAriaValueText={valuetext}
                        valueLabelDisplay="auto"
                        step={1}
                        min={0}
                        max={10}
                        sx={{ width: "75%" }}
                        onChange={(e) => setHowImportent(e.target.value)}
                      />
                      <Typography sx={{ fontWeight: "bold", pt: "3px" }}>
                        {" "}
                        10{" "}
                      </Typography>
                    </Stack>
                  </Box>

                  <Box
                    sx={{
                      bgcolor: "primary.extraLight",
                      padding: "25px",
                      pt: "30px",
                      pb: "60px",
                    }}
                  >
                    <Typography
                      variant="h4"
                      textAlign={"center"}
                      sx={{ mb: "15px" }}
                    >
                      {" "}
                      Finns det ngot som hindrar dig frn att ha {title} ssom
                      du vill ha det?{" "}
                    </Typography>
                    <TextField
                      fullWidth
                      id="obstacle"
                      label="Om Ja, skriva in vad som hindrar dig hr"
                      variant="outlined"
                      sx={{ backgroundColor: "white" }}
                      onChange={(e) => setObstacle(e.target.value)}
                    />

                    <Button
                      variant="contained"
                      aria-label="Backa"
                      startIcon={<ArrowBackIosIcon />}
                      onClick={() => goBackOneCard()}
                      sx={{
                        position: "absolute",
                        padding: "10px",
                        borderRadius: " 0  6px 0 6px",
                        left: "0px",
                        bottom: "0px",
                      }}
                    >
                      {" "}
                      Frra livsomrdet{" "}
                    </Button>

                    <Button
                      variant="contained"
                      aria-label="Backa"
                      endIcon={<ArrowForwardIosIcon />}
                      onClick={() => handleSubmit({ title: title })}
                      sx={{
                        position: "absolute",
                        padding: "10px",
                        borderRadius: "6px 0  6px 0",
                        right: "0px",
                        bottom: "0px",
                      }}
                    >
                      {" "}
                      Nsta livsomrde{" "}
                    </Button>
                  </Box>
                </form>
              </Box>
            );
          })}
        </Box>
        <BackButton goTo={"/valj-livsomraden"}>Tillbaka</BackButton>
        <Button
          variant="contained"
          disabled={!isItDone}
          color="success"
          aria-label="Nsta"
          endIcon={<ArrowForwardIosIcon />}
          onClick={nextPage}
          sx={{ float: "right", mb: "15px", mt: "45px", mr: "15px" }}
        >
          Nsta
        </Button>
        <Box sx={{ width: "100%", height: "15px", clear: "both" }}> </Box>
      </Container>
    </div>
  );
}

export default LifeAreasPriority;
